.empty {
  color: gray;

  display: flex;
  justify-content: center;
  align-items: center; }

.lesson {
  // border: 1px solid red
  margin-bottom: 8px;

  font-size: 14px;

  display: flex;
  align-items: center;

  &__time {
    // border: 1px solid green

    padding-right: 5px;

    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100%; }

  &__info {
    // border: 1px solid black
    // border-left: 5px solid red

    padding-left: 5px;

    flex: 6;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    .info {

      &__auditory,
      &__fromWhoms {
        font-size: 12px;
        color: gray; }

      &__subject {
        font-weight: bold;
        font-size: 16px; }

      &__fromWhoms {
        display: flex;
        justify-content: start;
        align-items: center;

        .fromWhoms__item {
          margin-right: 5px; } } } } }
